.footer {
  min-height: 280px;
  background-color: #041827;
  padding: 50px 0 43px;

  h3,
  button {
    margin-top: 7px;
    margin-bottom: 23px;
    font-family: $roboto-condensed-font;
    font-weight: 400;
    font-size: 15px;
    line-height: 16px;
    letter-spacing: 0.05em;
    color: #ffffff;
    text-transform: uppercase;
    user-select: none;
  }

  a,
  address {
    font-family: $pt-mono-font;
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    font-style: normal;
    color: rgba(255, 255, 255, 0.7);
  }

  @include vp-1023 {
    padding-left: 50px;
    padding-right: 45px;
    padding-bottom: 39px;

    h3 {
      margin-top: 0;
    }
  }

  @include vp-767 {
    padding: 35px 0 0;

    address {
      font-size: 14px;
      line-height: 19px;
    }
  }
}

.footer__container {
  @include container;
  display: flex;
  justify-content: space-between;

  @media (min-width: 1024px) and (max-width: 1240px) {
    margin-left: 10px;
    margin-right: 10px;
  }

  @include vp-1023 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 25px;
    row-gap: 37px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    gap: 0;
    margin-bottom: 35px;
  }
}

.footer__logo-socials-wrapper {
  @include vp-1023 {
    display: flex;
    justify-content: space-between;
    grid-column: span 2;
  }

  @include vp-767 {
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}

.footer__logo {
  @include vp-767 {
    margin-right: 5px;
    margin-bottom: 10px;
  }
}

.footer__socials {
  margin-top: 30px;
  margin-left: 10px;

  ul {
    display: grid;
    grid-template-columns: repeat(4, 40px);
    gap: 15px;
    margin: 0;
    padding: 0;
  }

  a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    cursor: pointer;
    user-select: none;

    &:hover,
    &:focus-visible {
      svg {
        transform: scale(1.2);
        transition: transform 0.2s;
      }
    }

    &:active {
      svg {
        fill: #00b2ff;
      }
    }
  }

  li {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  svg {
    width: 30px;
    height: 30px;
    fill: #f8fdff;
    transition: transform 0.2s;
  }

  @include vp-1023 {
    margin-top: 5px;
    margin-left: 0;

    ul {
      grid-template-columns: repeat(3, 40px);
    }
  }

  @include vp-767 {
    ul {
      display: flex;
      flex-wrap: wrap;
      margin-left: 3px;
      margin-bottom: 24px;
    }
  }
}

.footer__nav {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-left: auto;
  margin-right: 30px;

  h3 {
    grid-column: span 2;
  }

  ul {
    margin: 0;
    padding: 0;
    width: 235px;

    &:last-child {
      padding-left: 15px;
    }
  }

  li {
    margin: 0 0 9px;
    padding: 0;
    list-style-type: none;

    display: flex;
    align-items: center;

    &:last-child {
      align-items: flex-start;
    }

    &:nth-child(-n+4) {
      grid-column: 1 / 2;
    }

    &:nth-child(n+5):nth-child(-n+8) {
      grid-column: 2 / 3;
    }

    &:nth-child(n+9):nth-child(odd) {
      grid-column: 1 / 2;
    }

    &:nth-child(n+9):nth-child(even) {
      grid-column: 2 / 3;
    }
  }

  a {
    user-select: none;

    &:hover,
    &:focus-visible {
      color: #00b2ff;
    }

    &:active {
      color: #057daf;
    }
  }

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    margin: 0;

    ul {
      display: flex;
      flex-direction: column;
      width: auto;
      padding-right: 80px;

      &:last-child {
        padding-left: 0;
      }
    }
  }

  @include vp-767 {
    ul {
      &:last-child {
        padding-bottom: 6px;
      }
    }

    li {
      padding-left: 25px;
    }

    a {
      font-size: 14px;
      line-height: 19px;
    }
  }
}

.footer__accordion {
  @include vp-767 {
    h3 {
      position: relative;
      padding: 22px 50px 21px 25px;
      margin: 0;
      cursor: pointer;

      &::before {
        content: "";
        position: absolute;
        top: 46%;
        right: 26px;
        display: block;
        height: 1px;
        width: 15px;
        background-color: #f8fdff;
      }

      &::after {
        content: "";
        position: absolute;
        top: 47%;
        transform: translate(0, -50%);
        right: 33px;
        display: block;
        height: 15px;
        width: 1px;
        background-color: #f8fdff;
      }
    }

    ul {
      &:last-child {
        border-bottom: 1px solid rgba(248, 253, 255, 0.7);
      }
    }
  }
}

.footer__accordion-title {
  &--mobile {
    display: none;
  }

  @include vp-767 {
    &--desktop {
      display: none;
    }

    &--mobile {
      display: block;
    }
  }
}

.footer__accordion-title button {
  user-select: none;
  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
  cursor: pointer;

  &:hover,
  &:focus-visible {
    color: #00b2ff;
  }

  &:active {
    color: #057daf;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
  }
}

.footer__accordion.no-js {
  @include vp-767 {
    h3 {
      &::before,
      &::after {
        display: none;
      }
    }

    ul {
      display: flex;
    }
  }
}

.footer__accordion.is-opened {
  @include vp-767 {
    h3 {
      &::after {
        display: none;
      }
    }
  }
}

.footer__accordion.is-closed {
  @include vp-767 {
    h3 {
      border-bottom: 1px solid rgba(248, 253, 255, 0.7);

      &::after {
        display: block;
      }
    }

    ul {
      display: none;
    }
  }
}

.footer__office {
  display: flex;
  flex-direction: column;
  width: 345px;
  padding-right: 10px;

  ul {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
  }

  li {
    padding: 0;
    margin: 0 0 6px 0;
    list-style-type: none;

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    &:hover,
    &:focus-visible {
      color: #00b2ff;
    }

    &:active {
      color: #057daf;
    }
  }


  @include vp-1023 {
    width: auto;
    padding: 0;

    ul {
      display: flex;
      flex-direction: column;
    }
  }

  @include vp-767 {
    li {
      padding-left: 25px;

      :first-child {
        margin-bottom: 10px;
      }

      &:last-child {
        margin-bottom: 14px;
      }
    }

    address {
      margin-top: -5px;
    }

    a {
      font-size: 14px;
      line-height: 25px;
    }
  }
}
